class KeyCacheQueue {
  constructor() {
    this.cache = {};
    this.keyLock = {};
    this.keyLockDef = {};
    this.keyResolveSet = {};
  }
  /**
   *
   * @param {*} key //ä½ä¸ºæ¥è¯¢è®¡ç®çå¯ä¸æ è¯ï¼è®¤ä¸ºç¸åkeyè°ç¨å½æ°å°è¿åç¸åçç»æ
   * @param {*} fn(callback) //æ§è¡çå½æ°ï¼ callbackä¸ºå½æ°æ§è¡å®è°ç¨ççåè°å½æ°ï¼å¯å°è¿åå¼ä¼ å¥
   * @param {*} force  //å¿½ç¥ç¼å­ï¼æææ¥è¯¢è®¡ç®é½æ§è¡
   */
  push(key, fn, force) {
    return new Promise((resolve, reject) => {
      try {
        if (force) {
          this.execFn(fn, key, force, resolve);
          //æ è§ç¼å­
        } else {
          if (this.cache[key]) {
            resolve(this.cache[key]);
          } else {
            this.initKeyResolveSetIfNeed(key);
            this.keyResolveSet[key].push(resolve);
            if (!this.keyLock[key]) {
              //æ²¡æéä½ï¼éè¦è°ç¨å½æ°è¿è¡æ¥è¯¢
              this.keyLock[key] = true;
              this.execFn(fn, key);
            } //å¦ææ¯éä½çï¼åæ éæä½ï¼promise çresolve å½æ°å·²ç»å­è¿å» keyResolveSet äº
          }
        }
      } catch (e) {
        reject(e);
      }
    });
  }

  execFn(fn, key, force, resolve) {
    try {
      fn(result => {
        this.cache[key] = result;
        if (force) {
          resolve(result);
        } else {
          let cpSet = Array.apply(null, this.keyResolveSet[key]);
          this.keyResolveSet[key] = [];
          cpSet.forEach(f => {
            f(result);
          });
          this.keyLock[key] = false;
        }
      });
    } catch (e) {
      throw e;
    }
  }

  initKeyResolveSetIfNeed(key) {
    !this.keyResolveSet[key] && (this.keyResolveSet[key] = []);
  }
}

export default KeyCacheQueue;
